/** @format */
* {
  margin: 0;
}
*::-webkit-scrollbar {
  display: none;
}
.logo {
  background-color: antiquewhite;
  margin-top: 40px;
}
ul {
  width: 33.33%;
  margin: auto;
  margin-top: 10px;
  margin-right: -10px;
  text-align: center;
}
li {
  margin: 0px 0px 0 0px;
  width: 25%;
  display: inline-block;
  font-size: 8px;
  padding: 15px 0 12px 0;
}
li:hover {
  color: #8a8676;
  cursor: pointer;
}
